#contribute{display:flex;flex-direction:column;align-items:center;max-width:856px}.notice{font-size:.875rem;line-height:1.25rem;font-weight:400;letter-spacing:.0178571429em;margin-top:12px;padding:12px 24px;background-color:#ecf0f1;border-radius:3px}.notice,.title{font-family:Roboto,sans-serif;-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased;text-decoration:inherit;text-transform:inherit;color:rgba(0,0,0,.87);color:var(--mdc-theme-text-primary-on-light,rgba(0,0,0,.87));max-width:90vw}.title{font-size:1.25rem;line-height:2rem;font-weight:500;letter-spacing:.0125em}.desc,.title{margin-top:24px}.desc{display:grid;align-items:center;justify-items:center;grid-row-gap:24px}.desc-text{font-family:Roboto,sans-serif;-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased;font-size:1rem;line-height:1.5rem;font-weight:400;letter-spacing:.03125em;text-decoration:inherit;text-transform:inherit;color:rgba(0,0,0,.87);color:var(--mdc-theme-text-primary-on-light,rgba(0,0,0,.87));font-size:.938rem;max-width:90vw}.desc-text .ext-name{font-weight:500}.desc-image{max-width:60vw}.goals-wrap{margin-top:36px;width:90vw}.cta{font-size:1.25rem;line-height:2rem;font-weight:500;letter-spacing:.0125em;text-align:center}.cta,.goals{font-family:Roboto,sans-serif;-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased;text-decoration:inherit;text-transform:inherit;color:rgba(0,0,0,.87);color:var(--mdc-theme-text-primary-on-light,rgba(0,0,0,.87))}.goals{font-size:1rem;line-height:1.5rem;font-weight:400;letter-spacing:.03125em;font-size:.938rem;margin-top:24px;margin-bottom:12px}.goal{display:flex;align-items:center}.goal-bullet{font-size:36px;color:#1abc9c;margin-right:8px}.progress-details{display:flex;justify-content:space-between;font-family:Roboto,sans-serif;-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased;font-size:.75rem;line-height:1.25rem;font-weight:400;letter-spacing:.0333333333em;text-decoration:inherit;text-transform:inherit;color:rgba(0,0,0,.54);color:var(--mdc-theme-text-secondary-on-light,rgba(0,0,0,.54));font-weight:500;margin-top:12px}.progress-value{font-weight:700}.progress-token{width:auto;height:14px;vertical-align:-10%;opacity:.7}.goals-enter-active,.goals-leave-active{max-height:300px;margin-top:24px;transition:max-height .4s ease,margin-top .4s ease,opacity .3s ease}.goals-enter,.goals-leave-to{max-height:0;margin-top:0;opacity:0}.cta-buttons{display:grid;grid-row-gap:24px;margin-top:48px}.cta-coin{display:grid;align-items:center;justify-items:center;grid-template-columns:repeat(2,auto);grid-column-gap:8px;margin-top:32px;margin-bottom:24px;font-family:Roboto,sans-serif;-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased;font-size:1rem;line-height:1.75rem;font-weight:400;letter-spacing:.009375em;text-decoration:inherit;text-transform:inherit;font-weight:700;color:#3498db;cursor:pointer}.cta-coin:active,.cta-coin:hover{color:#2980b9}.contribute-button{display:flex;align-items:center;justify-content:center;width:192px!important;height:48px!important}.contribute-button:not(:disabled){background-color:#ffad01}.contribute-button img{width:auto;height:20px}.cta-coin img{width:20px;height:20px}@media(min-width:480px){.cta-buttons{grid-template-columns:repeat(2,1fr);grid-column-gap:56px;margin-top:56px}.desc-image{max-width:260px}}@media(min-width:576px){.title{font-family:Roboto,sans-serif;-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased;font-size:1.5rem;line-height:2rem;font-weight:400;letter-spacing:normal;text-decoration:inherit;text-transform:inherit}.desc-text{max-width:70vw}.goals-wrap{width:70vw}}@media(min-width:768px){.title{font-family:Roboto,sans-serif;-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased;font-size:2.125rem;line-height:2.5rem;font-weight:400;letter-spacing:.0073529412em;text-decoration:inherit;text-transform:inherit;color:rgba(0,0,0,.87);color:var(--mdc-theme-text-primary-on-light,rgba(0,0,0,.87));margin-top:48px}.desc{grid-template-columns:repeat(2,auto);grid-column-gap:56px;margin-top:72px}.desc-text{font-family:Roboto,sans-serif;-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased;font-size:1rem;line-height:1.75rem;font-weight:400;letter-spacing:.009375em;text-decoration:inherit;text-transform:inherit;max-width:400px}.desc-image{max-width:300px}.goals-wrap{margin-top:48px;width:100%}.cta{font-size:1.5rem;line-height:2rem;letter-spacing:normal}.cta,.goals{font-family:Roboto,sans-serif;-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased;font-weight:400;text-decoration:inherit;text-transform:inherit}.goals{font-size:1rem;line-height:1.75rem;letter-spacing:.009375em;margin-top:48px}.goal-bullet{font-size:48px}.progress-details{font-family:Roboto,sans-serif;-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased;font-size:.875rem;line-height:1.25rem;font-weight:400;letter-spacing:.0178571429em;text-decoration:inherit;text-transform:inherit}.progress-token{height:16px}.goals-enter-active,.goals-leave-active{margin-top:48px}}